<template>
    <div class="song-list">
        <ul class="song-list-header">
            <li v-for="(item,index) in songStyle" :key="index" @click="handleChangeView(item.name)"
                :class="{active:item.name==activeName}">
                {{item.name}}
            </li>
        </ul>
        <div>
            <content-list :contentList="data"></content-list>
            <div class="pagination">
                <el-pagination @current-change="handleCurrentChange" background layout="total,prev,pager,next"
                        :current-page="currentPage" :page-size="pageSize" :total="albumDatas.length">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
import ContentList from '../components/ContentList';
import {findAllSongSheet,getSongListOfLikeStyle} from '../api/index';
import {mixin} from "../mixins";
import {songStyle} from '../assets/data/songList';

export default {
    name: 'song-list',
    mixins: [mixin],
    components:{
        ContentList
    },
    data(){
        return{
            albumDatas: [],      //
            pageSize: 15,        //15
            currentPage: 1,      //
            songStyle: [],           //
            activeName: ''    //
        }
    },
    computed:{
        //
        data(){
            return this.albumDatas.slice((this.currentPage - 1)*this.pageSize,this.currentPage*this.pageSize)
        }
    },
    mounted(){
        this.songStyle = songStyle;
        this.getSongList();
    },

    methods:{
        getSongList(){            
            findAllSongSheet()
                .then(res =>{
                    this.currentPage = 1;
                    this.albumDatas = res.data;
                })           
        },
        //
        handleCurrentChange(val){
            this.currentPage = val;
        },
        //style
        handleChangeView(name){
            this.activeName = name;
            this.albumDatas = [];
            if(name == ''){
                this.getSongList();
            }else{
                this.goSongListOfStyle(name);
            }
        },
        //style
        goSongListOfStyle(style){
            getSongListOfLikeStyle(style)
                .then(res =>{
                        this.currentPage = 1;
                        this.albumDatas = res.data;
                    }) 
        },

    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/song-list.scss';
</style>


