<template>
    <div class="my-music">
        <div class="album-slide">
            <div class="album-img">
                <img :src="attachImageUrl(avator)">
            </div>
            <ul class="album-info">
                <li>:{{username}}</li>
                <li>:{{userSex}}</li>
                <li>{{birth}}</li>
                <li>{{location}}</li>
            </ul>
        </div>
        <div class="album-content">
            <div class="album-title">
                {{introduction}}
            </div>
            <div class="songs-body">
                <album-content :songList="collectList">
                    <template slot="title"></template>
                </album-content>
            </div>
        </div>
    </div>
</template>

<script>
import {mixin} from '../mixins';
import {mapGetters} from 'vuex';
import {getUserOfId,getCollectOfUserId,getSongBySongId} from '../api/index';
import AlbumContent from "../components/AlbumContent";

export default {
    name: 'my-music',
    mixins: [mixin],
    components:{
        AlbumContent
    },
    data(){
        return {
            avator: '',       //
            username: '',     //
            userSex: '',      //
            birth: '',        //
            location: '',     //
            introduction: '', //
            collection: [],     //
            collectList: [],    //
        }
    },
    computed:{
        ...mapGetters([
            'listOfSongs',      //
            'userId',           //id
        ])
    },
    mounted(){
        this.getMsg(this.userId);
        this.getCollection(this.userId);
    },
    methods:{
        getMsg(userId){
            getUserOfId(userId)
                .then(res =>{
                    this.avator = res.data.avator;
                    this.username = res.data.username;
                    if(res.data.sex==0){
                        this.userSex = '';
                    }else if (res.data.sex==1){
                        this.userSex = '';
                    }
                    this.birth = this.attachBirth(res.data.birth);
                    this.location = res.data.location;
                    this.introduction = res.data.introduction;                    
                })
                .catch(err => {
                    console.log(err);
                })
        },
        //
        getCollection(userId){
            getCollectOfUserId(userId)
                .then(res =>{
                        this.collection = res.data;
                        //id   
                        for(let item of this.collection){
                            this.getSongsOfIds(item.songId);
                        }             
                    })
                .catch(err => {
                    console.log(err);
                })
        },
        //id   
        getSongsOfIds(id){
            getSongBySongId(id)
                .then(res =>{
                        this.collectList.push(res.data);
                    })
                .catch(err => {
                    console.log(err);
                })
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/my-music.scss';
</style>


