<template>
    <div class="singer-album">
        <div class="album-slide">
            <div class="singer-img">
                <img :src="attachImageUrl(singer.pic)">
            </div>
            <ul class="info">
                <li v-if="singer.sex==0||singer.sex==1">{{attachSex(singer.sex)}}</li>
                <li>{{attachBirth(singer.birth)}}</li>
                <li>{{singer.location}}</li>
            </ul>
        </div>
        <div class="album-content">
            <div class="intro">
                <h2>{{singer.name}}</h2>
                <span>{{singer.introduction}}</span>
            </div>
            <div class="content">
                <album-content :songList="listOfSongs">
                    <template slot="title"></template>
                </album-content>
            </div>
        </div>
    </div>
</template>
<script>
import {mixin} from '../mixins';
import {mapGetters} from 'vuex';
import {getSongBySingerId} from '../api/index';
import AlbumContent from "../components/AlbumContent";
export default {
    name: 'singer-album',
    mixins: [mixin],
    components:{
        AlbumContent
    },
    data(){
        return {
            singerId: '',       //id
            singer: {},         //
        }
    },
    computed:{
        ...mapGetters([
            'listOfSongs',      //
            'tempList',         //
            'loginIn',          //
            'userId',           //id
        ])
    },
    created(){
        this.singerId = this.$route.params.id;
        this.singer = this.tempList;
        this.getSongOfSingerId();
    },
    methods:{
        //id
        getSongOfSingerId(){
            getSongBySingerId(this.singerId)
                .then(res => {
                    this.$store.commit('setListOfSongs',res.data);
                })
                .catch(err =>{
                    console.log(err)
                })
        },
        //
        attachSex(value){
            if(value==0){
                return ''
            }else if(value==1){
                return ''
            }
            return ''
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/singer-album.scss';
</style>



