<template>
    <div class="play-bar" :class="{show:!toggle}">
        <div @click="toggle=!toggle" class="item-up" :class="{turn: toggle}">
            <svg class="icon">
                <use xlink:href="#icon-jiantou-xia-cuxiantiao"></use>
            </svg>
        </div>
        <div class="kongjian">
            <!--  -->
            <div class="item" @click="prev()">
                <svg class="icon">
                    <use xlink:href="#icon-ziyuanldpi"></use>
                </svg>
            </div>
            <!--  -->
            <div class="item" @click="togglePlay">
                <svg class="icon">
                    <use :xlink:href="playButtonUrl"></use>
                </svg>
            </div>
            <!--  -->
            <div class="item" @click="next()">
                <svg class="icon">
                    <use xlink:href="#icon-ziyuanldpi1"></use>
                </svg>
            </div>
            <!--  -->
            <div class="item-img" @click="toLyric()">
                <img :src="picUrl"/>
            </div>
            <!--  -->
            <div class="playing-speed">
                <!--  -->
                <div class="current-time">{{nowTime}}</div>
                <div class="progress-box">
                    <div class="item-song-title">
                        <div>{{title}}</div>
                        <div>{{artist}}</div>
                    </div>
                    <div ref="progress" class="progress" @mousemove="mousemove">
                        <!--  -->
                        <div ref="bg" class="bg" @click="updatemove">
                            <div ref="curProgress" class="cur-progress" :style="{width:curLength+'%'}"></div>
                        </div>
                        <!--  -->
                        <div ref="idot" class="idot" :style="{left:curLength+'%'}" @mousedown="mousedown" @mouseup="mouseup"></div>
                    </div>
                </div>
                <!--  -->
                <div class="left-time">{{songTime}}</div>
                <!--  -->
                <div class="item item-volume">
                    <svg v-if="volume == 0" class="icon">
                        <use xlink:href="#icon-yinliangjingyinheix"></use>
                    </svg>
                    <svg v-else class="icon">
                        <use xlink:href="#icon-yinliang1"></use>
                    </svg>
                    <el-slider class="volume" v-model="volume" :vertical="true"></el-slider>
                </div>
                <!--  -->
                <div class="item" @click="collection">
                    <svg class="icon"  :class="{active:isActive}">
                        <use xlink:href="#icon-xihuan-shi"></use>
                    </svg>
                </div>
                <!--  -->
                <div class="item" @click="download()">
                    <svg class="icon">
                        <use xlink:href="#icon-xiazai"></use>
                    </svg>
                </div>
                <!--  -->
                <div class="item" @click="changeAside()">
                    <svg class="icon">
                        <use xlink:href="#icon-liebiao"></use>
                    </svg>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {mapGetters} from 'vuex';
import {mixin} from '../mixins/index'
import {download, getCollectOfUserId, setCollect} from '../api/index'

export default {
    name: 'play-bar',
    mixins: [mixin],
    data() {
        return {
            nowTime: '00:00',        //
            curLength: 0,            //
            songTime: '00.00',        // 
            progressLength: 0,        // 
            mouseStartX: 0,            // 
            tag: false,             //true
            flag: false,            //
            volume: 50,             //
            toggle: true            //
        }
    },
    computed:{
        ...mapGetters([
            'id',        // id
            'isPlay',    // 
            'url',        // url
            'playButtonUrl',    // 
            'title',                  //
            'artist',                 //
            'picUrl',                 //
            'duration',                //
            'curTime',             //
            'listOfSongs',          //list
            'listIndex',            //id
            'autoNext',              //
            'loginIn',              //
            'userId',               //id
            'isActive',             //
        ])
    },
    watch: {
        // 
        isPlay() {
            if (this.isPlay) {
                this.$store.commit('setPlayButtonUrl', '#icon-zanting');
            } else {
                this.$store.commit('setPlayButtonUrl', '#icon-bofang');
            }
        },
        // 
        curTime() {
            this.songTime = this.formatTime(this.duration);
            this.nowTime = this.formatTime(this.curTime);
            this.curLength = (this.curTime / this.duration) * 100;
        },
        // 
        volume() {
            this.$store.commit('setVolume', this.volume / 100);
        },
        autoNext() {
            this.next();
        }
    },
    mounted() {
        this.progressLength = this.$refs.progress.getBoundingClientRect().width;
        document.querySelector('.item-volume').addEventListener('click',function(e){
            document.querySelector('.volume').classList.add('show-volume');
            e.stopPropagation();
        },false);
        document.querySelector('.volume').addEventListener('click',function(e){
            e.stopPropagation();
        },false);
        document.addEventListener('click',function(){
            document.querySelector('.volume').classList.remove('show-volume');
        },false);
    },
    methods: {
        //
        notify(title,type) {
            this.$notify({
                title: title,
                type: type
            })
        },
        togglePlay() {
            if (this.isPlay) {
                this.$store.commit('setIsPlay', false);
            } else {
                this.$store.commit('setIsPlay', true);
            }
        },
        formatTime(value) {
            let time = parseInt(value);
            let result = '';
            let hour = parseInt(time / 3600);
            let minute = parseInt((time / 60) % 60);
            let second = parseInt(time % 60);
            if (hour > 0) {
                if (hour < 10) {
                    result = '0' + hour + ':';
                } else {
                result = hour + ':';
                }
            } else {
                result = '00:'
            }
            if (minute > 0) {
                if (minute < 10) {
                    result = result +  '0' + minute + ':';
                } else {
                result = result + minute + ':';
                }
            } else {
                result = result + '00:'
            }
            if (second > 0) {
                if (second < 10) {
                    result = result + '0' + second;
                } else {
                result = result + second;
                }
            } else {
                result = result + '00'
            }
            return result;
        },
        //
        mousedown(e){
            this.mouseStartX = e.clientX;
            this.tag = true;
        },
        //
        mouseup(){
            this.tag = false;
        },
        //
        mousemove(e){
            if(!this.duration){
                return false;
            }
            if(this.tag){
                let movementX = e.clientX - this.mouseStartX;       //
                let curLength = this.$refs.curProgress.getBoundingClientRect().width;
                let newPercent = ((movementX+curLength)/this.progressLength)*100;
                if(newPercent>100){
                    newPercent = 100;
                }
                this.curLength = newPercent;
                this.mouseStartX = e.clientX;
                this.changeTime(newPercent);
            }
        },
        //
        changeTime(percent){
            let newCurTime = (percent*0.01)* this.duration;
            this.$store.commit('setChangeTime',newCurTime);
        },
        //
        updatemove(e){
            if(!this.tag){
                //
                let curLength = this.$refs.bg.offsetLeft;
                let newPercent = ((e.clientX - curLength) / this.progressLength) * 100;
                if(newPercent>100){
                    newPercent = 100;
                }else if(newPercent<0){
                    newPercent = 0;
                }
                this.curLength = newPercent;
                this.changeTime(newPercent);
            }
        },
        //
        changeAside(){
            this.$store.commit('setShowAside',true);
        },
        //
        prev(){
            if(this.listIndex != -1 && this.listOfSongs.length > 1){    //
                if(this.listIndex > 0){                                 //
                    this.$store.commit('setListIndex',this.listIndex - 1);  //
                }else{                                                  //
                    this.$store.commit('setListIndex',this.listOfSongs.length - 1);  //
                }
                this.toplay(this.listOfSongs[this.listIndex].url);
            }
        },
        //
        next(){
            if(this.listIndex != -1 && this.listOfSongs.length > 1){    //
                if(this.listIndex < this.listOfSongs.length - 1){                                 //
                    this.$store.commit('setListIndex',this.listIndex + 1);  //
                }else{                                                      //
                    this.$store.commit('setListIndex',0);  //
                }
                this.toplay(this.listOfSongs[this.listIndex].url);
            }
        },
        //
        toplay: function(url){
            if(url && url != this.url){
                this.$store.commit('setId',this.listOfSongs[this.listIndex].id);
                this.$store.commit('setUrl',this.$store.state.configure.HOST+url);
                this.$store.commit('setPicUrl',this.$store.state.configure.HOST+this.listOfSongs[this.listIndex].pic);
                this.$store.commit('setTitle',this.replaceFName(this.listOfSongs[this.listIndex].name));
                this.$store.commit('setArtist',this.replaceLName(this.listOfSongs[this.listIndex].name));
                this.$store.commit('setLyric',this.parseLyric(this.listOfSongs[this.listIndex].lyric));
                this.$store.commit('setIsActive',false);
                if(this.loginIn){
                    getCollectOfUserId(this.userId)
                        .then(res =>{
                            for(let item of res.data){
                                if(item.songId == id){
                                    this.$store.commit('setIsActive',true);
                                    break;
                                }
                            }
                        })
                }
            }
        },
         //--
        replaceLName(str){
            let arr = str.split('-');
            return arr[0];
        },
        //--
        replaceFName(str){
            let arr = str.split('-');
            return arr[1];
        },
        toLyric() {
            this.$router.push('/Lyric');
        },
        //
        download() {
            download(this.url)
            .then(res=>{
                let content = res.data;
                let eleLink = document.createElement('a');
                eleLink.download = `${this.artist}-${this.title}.mp3`;
                eleLink.style.display = 'none';
                //blob
                let blob = new Blob([content]);
                eleLink.href = URL.createObjectURL(blob);
                //document
                document.body.appendChild(eleLink);
                //
                eleLink.click();
                //
                document.body.removeChild(eleLink);
            })
            .catch(err =>{
                console.log(err);
            })
        },
        //
        collection() {
            if(this.loginIn){
                var params = new URLSearchParams();
                params.append('userId',this.userId);
                params.append('type',0);
                params.append('songId',this.id);
                setCollect(params)
                    .then(res =>{
                        if(res.code == 20000){
                            this.$store.commit('setIsActive',true);
                            this.notify('','success');
                        }else if(res.code == 20003){
                            this.notify('','warning');
                        }else{
                            this.notify('','error');
                        }
                    })
            }else{
                this.notify('','warning');
            }
        }
    }
}
</script>
<style  lang="scss" scoped>
@import '../assets/css/play-bar.scss';
</style>


